@import "src/common/styles/sass/variables";

.projectsBlock {
     /*min-height: 100vh;*/
     background-color: $bgColor;
     .projectsContainer {
	    flex-direction: column;
	    justify-content: flex-start;
	    .projects {
		   width: 100%;
		   display: flex;
		   justify-content: space-between;
		   flex-wrap: wrap;
		   margin-top: 30px;
		   @media screen and (max-width: 915px) {
			  justify-content: center;
		   }
	    }
     }
}


